import React from 'react'
import ReactDOM from 'react-dom'
import './index.css'
import App from './App'
import reportWebVitals from './reportWebVitals'
import { BrowserRouter, createBrowserRouter, createRoutesFromElements, HashRouter, Route, RouterProvider } from 'react-router-dom'
import { Provider } from 'react-redux'
import { store } from './store/store'
import Orders from './views/Orders'
import NewOrder from './views/NewOrder'
import ProductsMgmt from './views/ProductsMgmt'

const router = createBrowserRouter(
  createRoutesFromElements(
    <Route exact path='/' element={<Orders />}>
      <Route exact path='new-order' element={<NewOrder />} />
      <Route exact path='orders' element={<Orders />} />
      <Route exact path='product-state' element={<ProductsMgmt />} />
    </Route>
  )
)

ReactDOM.render(
  <React.StrictMode>
      {/* <BrowserRouter > */}
      {/* <HashRouter basemname={`/`}> */}
    <Provider store={store}>
    <RouterProvider router={router} >
        <App />
    </RouterProvider>
    </Provider>
      {/* </HashRouter> */}
      {/* <BrowserRouter basemname={`/${process.env.PUBLIC_URL}`}> */}
      {/* </BrowserRouter> */}
  </React.StrictMode>,
  document.getElementById('root')
)
